@import "variables";
@import "mixin";
@import "header";
@import "deliveryPopup";
@import "fixed_nav";
@import "authorization";
@import "aside_buttons";

body {
  font-size: 20px;
  line-height: 22px;
  color: #474747;
  .content {
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      .content__actualProduct,
      .content__saleProduct {
        &__headline {
          margin-bottom: 10px;
          display: flex;
          justify-content: space-between;
          h5 {
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
          }
          div {
            display: flex;
            margin: 45px 40px 0 0;
            text-align: right;
            p {
              font: .875em/1em @sansregular;
              margin-right: 10px;
              span {
                display: inline-block;
                width: 130px;
                height: 25px;
                margin: 5px 5px 0;
                font: .875em/25px @sansregular;
                text-align: center;
                border: 1px solid @mainColor;
                border-radius: 5px;
                &:hover {
                  cursor: pointer;
                }
              }
              :nth-child(1) {
                color: #fff;
                background-color: @mainColor;
              }
            }
          }
        }
        &__slider{
          display: flex;
          img {
            width: 200px;
            height: 350px;
            border-radius: 5px;
          }
          .slider {
            width: 1340px;
            height: 350px;
            padding: 0 30px;
            position: relative;
            .lSSlideOuter {
              ul {
                .item-a,
                .item-b,
                .item-c,
                .item-e,
                .item-d,
                .item-f {
                  width: 250px;
                  height: 350px;
                }
                li {
                  text-align: center;
                  border: 1px solid #e6e6e6;
                  border-radius: 5px;
                  a {
                    display: block;
                    text-decoration: none;
                    img {
                      margin-top: 15px;
                      width: 200px;
                      height: 200px;
                    }
                    p {
                      padding: 10px;
                      font: 1em/1.125em @sansregular;
                      color: @linkColor;
                      text-align: left;
                    }
                    .price {
                      font: 1.25em/1.375em @sansbold;
                      span {
                        font: .875em/1em @sansregular
                      }
                    }
                  }
                  &:hover {
                    border-color: #176975;
                    cursor: pointer;
                    transition: .5s;
                  }
                }
              }
              .lSAction {
                a {
                  top: 40%;
                  height: 100px;
                  text-align: center;
                  z-index: 5;
                  border: 1px solid @mainColor;
                  border-radius: 10px;
                  background-image: none;
                  i{
                    font-size: 2em;
                    line-height: 100px;
                  }
                }
                .lSPrev{
                  left: 0;
                }
                .lSNext{
                  right: 0;
                }
              }
              .cS-hidden {
                height: 1px;
                opacity: 0;
                filter: alpha(opacity=0);
                overflow: hidden;
              }
            }
          }
        }
        &__showMore {
          display: flex;
          justify-content: flex-end;
          a {
            width: 225px;
            height: 40px;
            margin: 15px 50px;
            font: 1em/40px @sansregular;
            color: #176975;
            text-align: center;
            text-decoration: none;
            background-color: transparent;
            border: 1px solid #176975;
            border-radius: 5px;
            &:hover {
              color: #fff;
              background-color: #176975;
              cursor: pointer;
              transition: .5s;
            }
          }
        }
      }
      .content__howToMakeOrder {
        margin: 60px 0;
        display: flex;
        justify-content: space-between;
        div {
          width: 32.5%;
          height: 235px;
          display: flex;
          flex-wrap: wrap;
          background-color: #e6e6e6;
          border-radius: 5px;
          .icons {
            width: 40%;
            display: flex;
            justify-content: flex-end;
            i {
              font-size: 2em;
              color: #1b6b77;
            }
            .arrow {
              margin: 30px 0 0 0;
              width: 20%;
              text-align: right;
            }
            .cart,
            .box,
            .truck {
              margin-top: 40px;
              width: 100px;
              height: 100px;
              display: flex;
              justify-content: center;
              align-items: center;
              border: 2px solid #1b6b77;
              border-radius: 75px;
            }
          }
          .learnMore {
            width: 60%;
            display: flex;
            justify-content: center;
            align-items: center;
            a {
              width: 165px;
              height: 45px;
              font: .875em/45px @sansregular;
              color: #bababa;
              text-align: center;
              text-decoration: none;
              border: 1px solid #bababa;
              border-radius: 5px;
              &:hover {
                color: #fff;
                background-color: #1b6b77;
                transition: .5s;
              }
            }
          }
          .info {
            margin-left: 110px;
            width: 100%;
            font: 1em/1.125em @sansregular;
            color: #474747;
            span:nth-child(1) {
              font: 1.125em/1.25em @sansbold;
            }
          }
        }
      }
    }
  }
  .contentAddres {
    display: flex;
    iframe {
      width: 60%;
      height: 600px;
    }
    &__contactForm {
      width: 40%;
      display: flex;
      flex-wrap: wrap;
      background-color: #176975;
      .comentary {
        margin: 45px 0 0 45px;
        input {
          padding-left: 15px;
          font: 1.125em/45px @sansregular;
          color: #828282;
          border: 1px solid #176975;
          border-radius: 10px;
          outline: none;
        }
        input[type*="text"] {
          margin: 0 5px 10px;
          width: 245px;
          height: 45px;
        }
        textarea {
          margin: 0 5px 10px;
          padding-left: 15px;
          width: 515px;
          height: 100px;
          font: 1.125em/45px @sansregular;
          color: #828282;
          border: 1px solid #176975;
          border-radius: 10px;
          outline: none;
          resize: none;
        }
        button {
          margin: 20px 0 0 5px;
          width: 165px;
          height: 45px;
          font: 1.125em/40px @sansregular;
          color: #fff;
          text-align: center;
          background: transparent;
          border: 1px solid #fff;
          border-radius: 10px;
          outline: none;
          &:hover {
            cursor: pointer;
          }
        }
      }
      .contacts {
        margin-left: 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        p {
          font: 1.125em/1.25em @sansregular;
          color: #fff;
          a {
            color: #fff;
            text-decoration: none;
            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
      .social {
        margin-left: 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        ul {
          display: flex;
          justify-content: space-between;
          a {
            margin: 0 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 50px;
            height: 50px;
            font-size: 1.5em;
            color: #176975;
            text-decoration: none;
            background-color: #fff;
            border-radius: 75px;
            &:hover{
              color: #fff;
              background-color: @hoverColor;
              transition: .5s;
            }
          }
        }
        p {
          color: #fff;
          text-align: right;
          font: 1em/1.125em @sansbold;
          span:nth-child(1) {
            font: .875em/1em @sansregular;
          }
        }
      }
    }
  }
  .footer {
    background-color: @hoverColor;
    .containerParent();
    .container {
      .containerChild();
      padding: 35px 0 40px;
      display: flex;
      flex-direction: column;
      p {
        padding: 5px 0;
        font: 1em/1.125em @sansregular;
        color: #fff;
      }
    }
  }
}
